function setFetchedReducer(state, { payload }) {
  //      ,       
  const injectedHotels = payload.hotels.map((hotel) => ({ ...hotel, info: payload.info }));
  return {
    ...state,
    fetched: {
      hotels: injectedHotels,
      info: payload.info,
    },

  };
}

export default setFetchedReducer;
